<template>
  <div class="question-details-container">
    <el-row>
      <el-col :span="6">
        <div class="grid-content">【题型】：{{ QuestionType }}</div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content">【编号】：{{ questionDetail.id }}</div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content">【难度】：{{ Difficulty }}</div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content">【标签】：{{ questionDetail.tags }}</div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content">
          【学科】：{{ questionDetail.subjectName }}
        </div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content">
          【目录】：{{ questionDetail.directoryName }}
        </div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content">【方向】：{{ questionDetail.direction }}</div>
      </el-col>
    </el-row>
    <hr />

    <el-row>
      <el-col class="p0" :span="24">
        <div class="grid-content">【题干】：</div>
        <div
          class="grid-content"
          style="color: blue"
          v-html="questionDetail.question"
        ></div>
        <div v-if="questionDetail.questionType == 1">
          <div class="question-body">
            {{ QuestionType }}题 选项：（以下选中的选项为正确答案）
          </div>
          <el-radio-group :value="radio">
            <el-radio
              v-for="option in questionDetail.options"
              :key="option.id"
              :label="option.isRight"
              >{{ option.title }}</el-radio
            >
          </el-radio-group>
        </div>
        <div v-else-if="questionDetail.questionType == 2">
          <el-checkbox-group :value="checkList">
            <el-checkbox
              v-for="option in questionDetail.options"
              :key="option.id"
              :label="option.isRight"
              >{{ option.title }}</el-checkbox
            >
          </el-checkbox-group>
        </div>
      </el-col>
    </el-row>
    <hr />
    <el-row>
      <el-col class="p0" :span="24">
        <div class="grid-content">
          【参考答案】：
          <el-button
            type="danger"
            size="small"
            @click=";(isVideoShow = true), $refs.video.play()"
            >视频答案预览</el-button
          >
        </div>
      </el-col>
    </el-row>
    <div class="video" v-show="isVideoShow">
      <video :src="questionDetail.videoURL" controls ref="video"></video>
    </div>
    <hr />
    <el-row>
      <el-col class="p0" :span="24">
        <div class="answer">
          <span>【参考答案】：</span>
          <span v-html="questionDetail.answer"></span>
        </div>
      </el-col>
    </el-row>

    <hr />
    <el-row>
      <el-col class="p0" :span="24">
        <div class="grid-content">
          【题目备注】：{{ questionDetail.remarks }}
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { createNamespacedHelpers, mapGetters } from 'vuex'
const { mapActions } = createNamespacedHelpers('questions')

export default {
  props: {
    questionId: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      radio: 1,
      checkList: [1],
      isVideoShow: false
    }
  },
  async created() {
    await this.UpdateQuestionDetail({ id: this.questionId })
  },
  methods: {
    ...mapActions(['UpdateQuestionDetail'])
  },
  computed: {
    ...mapGetters(['questionDetail']),
    QuestionType() {
      return ['', '单选', '多选', '简答'][this.questionDetail.questionType]
    },
    Difficulty() {
      return ['', '简单', '一般', '困难'][this.questionDetail.difficulty]
    }
  }
}
</script>

<style scoped lang="less">
.question-details-container {
  .el-col {
    padding: 10px 0;
    .question-body {
      margin-bottom: 5px;
    }
    .el-radio,
    .el-checkbox {
      padding: 8px 0;
    }
    .answer {
      display: flex;
      align-items: center;
    }
  }
  .el-radio-group,
  .el-checkbox-group {
    display: flex;
    flex-direction: column;
  }
  .video {
    width: 400px;
    height: 300px;
    video {
      width: 100%;
      height: 100%;
    }
  }
  .p0 {
    padding: 0;
  }
}
</style>
